import React from 'react';
import { Box, Button, TextField, Typography } from '@mui/material';
import './Home.scss';
import { Link } from 'react-router-dom';
import { useUser } from '../../hooks/UserProvider';

const HomePage = () => {
  const { userName } = useUser();

  return (
    <Box className="home-page">
      <Box
        component="img"
        src="/images/moveToPermanent.jpg"
        alt="Background"
        className="background-image"
        sx={{
          height: {
            xs: '50vh',
            sm: '75vh',
            md: '100vh',
          },
        }}
      />

      <Button
        variant="outlined"
        className="appointment-button"
        component={Link}
        to={userName ? "/ChoiceTreatment" : "/Auth"}
      >
        קביעת תור
      </Button>

      {/* מקטע אודות */}
      <Box id="about-section" className="about-section">
        <Typography variant="h4" component="h2" gutterBottom>
          אודות
        </Typography>
        <Typography variant="body1">
          ברוכים הבאים לקליניקה שלנו! אנו מציעים מגוון טיפולים מקצועיים באווירה נעימה ומרגיעה.
          המטרה שלנו היא להעניק לכם חוויה ייחודית ולספק לכם את השירות הטוב ביותר.
        </Typography>
      </Box>

      {/* מקטע יצירת קשר */}
      <Box id="contact-section" className="contact-section">
        <Typography variant="h4" component="h2" gutterBottom>
          יצירת קשר
        </Typography>
        <Typography variant="body1" className="contact-text">
          נשמח לשמוע מכם! צרו קשר דרך הטופס למטה או בטלפון: 03-1234567
        </Typography>
        <Box component="form" className="contact-form">
          <TextField label="שם מלא" variant="outlined" fullWidth />
          <TextField label="אימייל" variant="outlined" fullWidth />
          <TextField
            label="הודעה"
            variant="outlined"
            multiline
            rows={4}
            fullWidth
          />
          <Button variant="contained" color="primary" className="submit-button">
            שלח
          </Button>
        </Box>

      </Box>
    </Box>
  );
};

export default HomePage;



